import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
    {
        path: 'clientSalesDispatch',
        loadChildren: () => import('./client-sales-dispatch/client-sales-dispatch.module').then(m => m.ClientSalesDispatchModule),
        data: { title: 'Client Sales Dispatch', titleI18n: 'Client Sales Dispatch' },
    },

]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class DispatchManagementRoutingModule { }